.footer-container {
  position: relative;
  width: 80vw;
  margin: 0 10vw;
  height: 200px;
  display: flex;

  .logo-container {
      height: 100%;
      width: 15vw;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: #EEEFF4;
  }

  .footer-links-container {
      height: 100%;
      width: 50vw;
      background-color: #EEEFF4;
      display: flex;
      align-items: center;

      .nav-link {
          color: #7d828f;
          text-decoration: none;
          font-size: 18px;
          font-weight: 700;
          margin-left: 5vw;

          &:hover {
              color: black;
          }
      }
  }

  .button-link {
      position: absolute;
      right: 0;
      top: 35%;
  }
}

@media screen and (max-width: 1000px) {
  .footer-container {
      position: relative;
      width: 96vw;
      margin: 0;
      height: 150px;
      display: flex;
  
      .logo-container {
          height: 100%;
          width: 15vw;
          display: flex;
          align-items: center;
          justify-content: center;
          background-color: black;
      }
  
      .footer-links-container {
          height: 100%;
          width: 70vw;
          background-color: #EEEFF4;
          display: flex;
          align-items: center;
  
          .nav-link {
              color: #7d828f;
              text-decoration: none;
              font-size: 18px;
              font-weight: 700;
              margin-left: 5vw;
  
              &:hover {
                  color: black;
              }
          }
      }
  
      .button-link {
          position: absolute;
          right: 0;
          top: 25%;
      }
  }
}

@media screen and (max-width: 675px) {
  .footer-container {
      position: relative;
      width: 100vw;
      margin: 0;
      height: 400px;
      display: flex;
      flex-direction: column;
  
      .logo-container {
          z-index: 3;
          transform: translateY(-50%);
          height: 25vw;
          width: 25vw;
          margin: 0 auto;
          display: flex;
          align-items: center;
          justify-content: center;
          background-color: black;

          .logo {
              width: 20vw;
          }
      }
  
      .footer-links-container {
          position: absolute;
          padding-top: 18vw;
          height: 100%;
          width: 100vw;
          background-color: #EEEFF4;
          display: flex;
          flex-direction: column;
          align-items: center;
  
          .nav-link {
              color: #7d828f;
              text-decoration: none;
              font-size: 26px;
              font-weight: 700;
              margin-left: 0vw;
              margin-bottom: 25px;
  
              &:hover {
                  color: black;
              }
          }
      }
  
      .button-link {
          position: relative;
          margin: 30px auto;
      }
  }
}